<template>
   <div class = "shopcart">
       <div class="content"> <!-- 右边固定宽度，左边自适应 -->
            <div class="content-left">
                <div class="logo-wrapper">
                    <div class="logo" :class="{'highlight':totalCount>0}">
                         <!-- 绑定在条件下高亮的属性 -->
                        <i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
                    </div>
                    <div class="num" v-show="totalCount>0">{{totalCount}}</div>
                </div>
                <div class="price" :class="{'highlight':totalCount>0}">¥{{totalPrice}}</div>
                <div class="desc">另需配送费¥{{deliveryPrice}}元</div>
            </div>
            <div class="content-right">
                <div class="pay" :class="{'enough':totalPrice >= minPrice}">
                    {{payDesc}}
                </div>
            </div>
       </div>
       <div class="ball-container">
            <div transition="drop"  class="ball" v-for="ball in balls" v-show="ball.show">
                <div class="inner"></div>
            </div>
       </div>
   </div>
</template>

<script>
/* 导出组件 */
 export default {
    props: {
        selectFoods: {
            type: Array,
            default() {
                return [
                 /* {
                    price: 10,
                    count: 10
                }
                调试的数据*/
                ];
            }
        },
        deliveryPrice: {
            type: Number,
            default: 0
        },
        minPrice: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            balls: [
            {
                show: false
            },
            {
                show: false
            },
            {
                show: false
            },
            {
                show: false
            },
            {
                show: false
            }
            ]
        };
    },

    computed: {
        totalPrice() {
            let total = 0;
            this.selectFoods.forEach((food) => {
                total += food.price * food.count;
            });
            return total;
        },
        totalCount() {
            let count = 0;
            this.selectFoods.forEach((food) => {
                count += food.count;
            });
            return count;
        },
        payDesc() {
            if (this.totalPrice === 0) {
                return `￥${this.minPrice}元起送`;  /* ！！！注意：这里用单大括号，下同 */
                /* 这是ES6的一个语法，比用在拼接字符串了*/
            } else if (this.totalPrice < this.minPrice) {
                let diff = this.minPrice - this.totalPrice;
                return `还差￥${diff}元起送`;
            } else {
                return '去结算';
            }
        }
    },
    methods: {

    }
 };
</script>

<style lang="stylus" rel="stylesheet/stylus">
    .shopcart
        position:fixed /* 在显示goods组件时，相对于整个屏幕固定，而不是goods区域*/
        left:0
        bottom:0
        z-index:50
        width:100%
        height:48px
        background:#000
        .content
            display:flex
            background:#141d27
            font-size:0  /* 当子元素为inline-block布局时，为消除间隙*/
            .content-left
                flex:1
                .logo-wrapper
                    display: inline-block
                    position:relative
                    top:-10px
                    vertical-align:top   //注意 不是verticle
                    padding:6px
                    margin:0 12px
                    width:56px
                    height:56px
                    box-sizing:border-box   // 此处采用的是IE盒子模型*/
                    border-radius:50%
                    background: rgb(20,29,39)
                    .logo
                        width:100%
                        height:100%
                        border-radius:50%
                        text-align:center
                        background:#2b343c
                        &.highlight
                            background:rgb(0,160,220)
                        .icon-shopping_cart
                            line-height:44px
                            font-size:24px
                            color:#80858a
                            &.highlight
                                color: #fff
                    .num
                        position:absolute
                        top:0
                        right:0
                        width:24px
                        height:16px
                        line-height:16px
                        text-align:center
                        border-radius:16px
                        font-size:9px
                        font-weight:700
                        color: #fff
                        background:rgb(240,20,20)
                        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4)
                .price
                    display:inline-block
                    vertical-align: top
                    margin-top:12px
                    line-height:24px
                    padding-right:12px
                    box-sizing:border-box
                    border-right:1px solid rgba(255,255,255,0.1)
                    font-size:16px
                    font-weight:700
                    color:rgba(255,255,255,0.4)
                    &.highlight
                        color:#fff
                .desc
                    display: inline-block
                    vertical-align:top
                    margin:12px 0 0 10px
                    line-height:24px
                    color: rgba(255,255,255,0.4)
                    font-size: 12px
            .content-right
                flex:0 0 105px
                width:105px
                .pay
                    height:48px
                    line-height:48px
                    text-align:center
                    font-size:12px
                    font-weight:700
                    color:rgba(255,255,255,0.4)
                    background:#2b333b
                    &.enough
                        background:#00b43c
                        color:#fff
        .ball-container
            .ball
                position:fixed
                left:32px
                bottom:22px
                z-index:200
                &.drop-transition
                    transition:all 0.4s
                    .inner
                        width:16px
                        height:16px
                        border-radius:50%
                        background:rgb(0,160,220)
                        transition:all 0.4s


</style>


